    display: flex;
    justify-content: space-between;
    align-items: center;

    .important-num {
      font-size: 32rpx;
      font-weight: bolder;
    }
  }

  .inner-box {
    display: flex;
    justify-content: flex-start;
    margin-top: 33rpx;

    .list-box {
      width: calc((100% - 72rpx) / 7);
      margin-right: 12rpx;

      .list-card {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
