        border-radius: 4rpx;
        background: rgb(142 154 167 / 28%);

        .point-num {
          font-size: 22rpx;
          color: #92969a;
          margin-bottom: 13rpx;
        }
      }

      .today-completed {
        background: #4e6175;

        .point-num {
          color: #fff;
        }
      }

      .date {
        text-align: center;
