<template>
  <view class="card-content">
    <view class="tab-content">
      <sp-tabs id="tab-content" :menus="tabList" @on-click="toTabChange" />
    </view>
    <!--  -->
    <coupon-list v-if="tabStatus === '0'" :list="couponListData" />

    <!--  -->
    <offline-coupon-list v-if="tabStatus === '2'" :list="cardList" />

    <view v-if="isEnd && !loading" class="no-more">{{ tabStatus === '0' ? '-  -' : '-  -' }}</view>
  </view>
</template>

<script lang="ts" setup>
import { ref, reactive } from 'vue'
import { onLoad, onPullDownRefresh, onReachBottom } from '@dcloudio/uni-app'
import { getCardList, getUserCoupon } from '@api/card'
import offlineCouponList from './offline-coupon-list.vue'
import couponList from './coupon-list.vue'

const tabList = [
  { name: '', value: '0' },
  { name: '', value: '1' },
  { name: '', value: '2' }
]
const couponListData: CouponInfo[] = reactive([]) // 
const cardList: CouponInfo[] = reactive([]) // 
const pageNo = ref(1) // 
const loading = ref(false)
const pageSize = 10 // 
let total = 0
const isEnd = ref(false)
const tabStatus = ref('0') // tab 0 1 2

// 
function init() {
  pageNo.value = 1
  isEnd.value = false
  cardList.length = 0
  couponListData.length = 0
}
function getListInfo() {
  switch (tabStatus.value) {
    case '0': {
      getCouponListInfo()
      break
    }
    case '1': {
      return
    }
    case '2': {
      getCardListInfo()
      break
    }
  }
}

// tab
function toTabChange(index: string) {
  tabStatus.value = index
  init()
  getListInfo()
}
// 
async function getCouponListInfo() {
  if (tabStatus.value !== '0') {
    return
  }
  uni.showLoading({
    title: ''
  })
  loading.value = true
  try {
    const res = await getUserCoupon({ pageNo: pageNo.value, pageSize: pageSize })
    const coupon: CouponInfo[] = res.data
    uni.stopPullDownRefresh()
    uni.hideLoading()
    pageNo.value === 1 && couponListData.splice(0)
    total = res.total
    couponListData.push(...coupon)
    if (total === couponListData.length || total === 0) {
      isEnd.value = true
    }
    loading.value = false
  } catch (err) {
    uni.hideLoading()
  }
}
// 
async function getCardListInfo() {
  if (tabStatus.value !== '2') {
    return
  }
  uni.showLoading({
    title: ''
  })
  loading.value = true
  try {
    const res = await getCardList({ pageNo: pageNo.value, pageSize: pageSize })
    const card: CouponInfo[] = res.data
    uni.stopPullDownRefresh()
    uni.hideLoading()
    pageNo.value === 1 && cardList.splice(0)
    total = res.total
    cardList.push(...card)
    if (total === cardList.length || total === 0) {
      isEnd.value = true
    }
    loading.value = false
  } catch (err) {
    uni.hideLoading()
  }
}

onLoad(() => {
  getListInfo()
})
// 
onPullDownRefresh(() => {
  init()
  getListInfo()
})
//
onReachBottom(() => {
  if (!isEnd.value) {
    pageNo.value++
    getListInfo()
  }
})
</script>

<style lang="scss" scoped>
.card-content {
  .tab-content {
    position: fixed;
    width: 750rpx;
    z-index: 10;
    font-family: SourceHanSansSC-Regular, SourceHanSansSC;
  }

  .no-more {
    text-align: center;
    font-size: 26rpx;
    font-weight: 400;
    color: #909ca8;
    padding-bottom: calc(18rpx + env(safe-area-inset-bottom));
  }
}

.tab-content {

  /* #ifdef MP-WEIXIN */
  top: 0;
  /* #endif */

  /* #ifdef H5 */
  top: calc(44px + env(safe-area-inset-top));
  /* #endif */
}
</style>
